"use client";
import { Task } from "@/types/data";
import React, { useState } from "react";

interface MainListProps {
  tasks: Task[];
}

function MainList({ tasks }: MainListProps) {
  const [toUpdate, setToUpdate] = useState<number>(-1);

  async function deleteTask(id: number) {
    const res = await fetch(`/api/task/${id}`, {
      method: "DELETE",
    }).then((res) => res.json());

    console.log(res);
  }

  return (
    <div className="main-list">
      {toUpdate > 0 && (
        <div>
          <form action={"/api/task/" + toUpdate} method="PATCH">
            <label>
              <span>Nome: </span>
              <input type="text" name="name" />
            </label>
            <label>
              <span>Descrição: </span>
              <textarea name="description" id=""></textarea>
            </label>
            <button type="submit">Atualizar</button>
          </form>
        </div>
      )}

      {tasks &&
        tasks.length > 0 &&
        tasks.map((item) => (
          <div key={item.id} className="main-list__item">
            <h3>{item.name}</h3>
            <p>{item.description}</p>
            <button onClick={() => setToUpdate(item.id)}>Editar Tarefa</button>
            <button onClick={() => deleteTask(item.id)}>Excluir Tarefa</button>
          </div>
        ))}
    </div>
  );
}

export default MainList;
